

.App {
  text-align: center;
}

h1 {
  color: black;
  text-align: center;
  padding: 2%;
}

.cardResponsive {
  width: 18rem;
  margin: 2%;
}

.buttonLink {
  color: white;
  border-radius: 9px;
  padding: 3%;
  background: rgb(138, 43, 226);
  font-weight: bold;
}

.wrapperCharacter {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  justify-items: center;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 2%;
}

.cardDetail {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 3%;
  background: rgb(225, 246, 255);
  border-radius: 10px;
  box-shadow: 0 0 18px #2a264f;
}

h2 {
  text-align: center;
}

.wrapImage {
  display: -webkit-flex;
  display: flex;
}
.imageCharacter {
  border-radius: 8%;
  box-shadow: 0 0 18px #2a264f;
  text-align: center;
  -webkit-align-items: center;
          align-items: center;
}
.wrappImage {
  -webkit-flex-direction: column;
          flex-direction: column;
}

.wrappText {
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: justify;
  padding: 2%;
  max-width: 680px;
}

label {
  font-size: 20px;
  font-weight: bold;
}

p {
  font-size: 22px;
}

